
import "./billing.scss";
import Grid from '@material-ui/core/Grid';
import BarBilling from "../../components/chartjs/BarBilling";
import DoughnutBilling from "../../components/chartjs/DoughnutBilling";

export default function Billing(params) {

  return <div className="billing">
    <div className="top">
      <h1>Billing</h1>
      <span>Welcome back, Lucy! We've missed you. 👋</span>
    </div>
    <div className="body">
      <Grid container >
        <Grid item xs={12} sm={12} md={4}>
          <Grid container >
            <Grid item xs={12} sm={6}>
              <div className="section middle">
                <span>Ingresos</span>
                {/* <h1>{ingresos}</h1> */}
              </div>
            </Grid>
            <Grid item xs={12} sm={6}>
              <div className="section middle">
                <span>Retiros</span>
                {/* <h1>{egresos}</h1> */}
              </div>
            </Grid>
            <Grid item xs={12} sm={6}>
              <div className="section middle">
                <span>Saldo Actual</span>
                {/* <h1>{total}</h1> */}
              </div>
            </Grid>
            <Grid item xs={12} sm={6}>
              <div className="section middle">
                <span>Follows</span>
                {/* <h1>{randomInteger({ min: 0, max: 10000 })}</h1> */}
              </div>
            </Grid>
          </Grid>
        </Grid>
        <Grid item xs={12} sm={12} md={8}>
          <div className="section">
            <div>
              <span>Billings</span>
            </div>
            <div className="grafo">
              <BarBilling />
            </div>
          </div>
        </Grid>
        <Grid item xs={12} sm={12} md={8}>
          <div className="section">
            <span>Map</span>
          </div>
        </Grid>
        <Grid item xs={12} sm={12} md={4}>
          <div className="section">
            <div>
              <span>Source / Medium</span>
            </div>
            <div className="grafo">
              <DoughnutBilling />
            </div>
          </div>
        </Grid>
      </Grid>
    </div>
  </div>
}