import "./listItem.scss";
import { useState, useEffect } from "react";
import MultimediaInfo from "../multimediaInfo/MultimediaInfo";
import { getMultimedia, getMultimediaPoster } from "../../apis/multimedia";

export default function ListItem({ /*index,*/ multimedia }) {
  const [isHovered, setIsHovered] = useState(false);
  const [url, setUrl] = useState(null);
  const [urlPoster, setUrlPoster] = useState(null);
  const token = localStorage.getItem("user");
  useEffect(() => {
    if (multimedia?.multimediaId && multimedia?.mimetypeId > 1) {
      getMultimedia(multimedia.multimediaId).then((response) => {
        setUrl(URL.createObjectURL(new Blob([response], { type: response.type })));
      })
    } else {
      getMultimediaPoster(multimedia.multimediaId).then((response) => {
        setUrlPoster(URL.createObjectURL(new Blob([response], { type: response.type })));
      })
    }
  }, [multimedia?.multimediaId, multimedia?.mimetypeId])

  return (<>
    <div
      className="listItem"
      onMouseOver={() => setIsHovered(true)}
      onMouseOut={() => setIsHovered(false)}
    >
      {multimedia?.multimediaId && multimedia?.mimetypeId === 1 ? <>
        <video className="poster"
          poster={urlPoster}
          src={`${process.env.REACT_APP_URL_API}/multimedia/${multimedia?.multimediaId}/${token}`}
          alt=""
          playsInline
          loop
          muted
          controlsList="nodownload"
          onContextMenu={(e) => e.preventDefault()}
          onMouseOver={event => event.target.readyState > 1 && event.target?.play()}
          onMouseOut={event => event.target?.pause()}
        />
        <div className="hover" style={{ display: isHovered ? 'block' : 'none' }}>
          <MultimediaInfo
            isModal={false}
            reactionId={multimedia?.multimediaReaction?.reactionId}
            multimedia={multimedia}
          />
        </div>
      </> : <>
        {url && <img
          className="poster"
          src={url}
          alt=""
        />}
      </>}
    </div>
  </>
  );
}
